<template><section><h1>Model</h1>
<p>The <code v-pre>vc-primitive-model</code> component is used to load a 3D model based on glTF, the runtime asset format for WebGL, OpenGL ES, and OpenGL.</p>
<h2>Example</h2>
<h3>Load a model with <code v-pre>vc-primitive-model</code></h3>
<h4>Preview</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready">
        <vc-primitive-model ref="model" @readyPromise="readyPromise" :url="url" :modelMatrix="modelMatrix" :scale="10000" :minimumPixelSize="128" :maximumScale="200000">
        </vc-primitive-model>
      </vc-viewer>
    </div>
  </template>
  
</doc-preview>
<h4>Code</h4>
<pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"viewer"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">vc-viewer</span> <span class="hljs-attr">@ready</span>=<span class="hljs-string">"ready"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">vc-primitive-model</span>
        <span class="hljs-attr">ref</span>=<span class="hljs-string">"model"</span>
        <span class="hljs-attr">@readyPromise</span>=<span class="hljs-string">"readyPromise"</span>
        <span class="hljs-attr">:url</span>=<span class="hljs-string">"url"</span>
        <span class="hljs-attr">:modelMatrix</span>=<span class="hljs-string">"modelMatrix"</span>
        <span class="hljs-attr">:scale</span>=<span class="hljs-string">"10000"</span>
        <span class="hljs-attr">:minimumPixelSize</span>=<span class="hljs-string">"128"</span>
        <span class="hljs-attr">:maximumScale</span>=<span class="hljs-string">"200000"</span>
      &gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">vc-primitive-model</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">vc-viewer</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data() {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">url</span>: <span class="hljs-string">'./statics/SampleData/models/CesiumAir/Cesium_Air.gltf'</span>,
        <span class="hljs-attr">modelMatrix</span>: {}
      }
    },
    <span class="hljs-attr">methods</span>: {
      ready(cesiumInstance) {
        <span class="hljs-keyword">const</span> { Cesium, viewer } = cesiumInstance
        <span class="hljs-keyword">this</span>.viewer = viewer
        <span class="hljs-keyword">this</span>.modelMatrix = Cesium.Transforms.eastNorthUpToFixedFrame(Cesium.Cartesian3.fromDegrees(<span class="hljs-number">105</span>, <span class="hljs-number">38</span>, <span class="hljs-number">10000</span>))
      },
      readyPromise(model) {
        <span class="hljs-keyword">const</span> boundingSphere = Cesium.BoundingSphere.transform(model.boundingSphere, model.modelMatrix)
        <span class="hljs-keyword">this</span>.viewer.scene.camera.flyToBoundingSphere(boundingSphere)
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
<h2>Instance Properties</h2>
<!-- prettier-ignore -->
<p>|name|type|default|description|                                                                                     |
| ------------------------- | ------- | ------ | ---------------------------------------------------------------------------------------------- |
| url                       | String  |        | <code v-pre>required</code> The url to the .gltf file.                                                         |
| basePath                  | String  |        | <code v-pre>optional</code> The base path that paths in the glTF JSON are relative to.                                           |
| show                      | Boolean | <code v-pre>true</code> | <code v-pre>optional</code> Determines if the model primitive will be shown. |
| modelMatrix               | Object  |        | <code v-pre>optional</code> The 4x4 transformation matrix that transforms the model from model to world coordinates. |
| scale                     | Number  | <code v-pre>1.0</code>  | <code v-pre>optional</code> A uniform scale applied to this model.                                                               |
| minimumPixelSize          | Number  | <code v-pre>0.0</code>  | <code v-pre>optional</code> The approximate minimum pixel size of the model regardless of zoom. |
| maximumScale              | Number  |        | <code v-pre>optional</code> The maximum scale for the model.                                                              |
| id                        | *      |        | <code v-pre>optional</code> A user-defined object to return when the model is picked with Scene#pick. |
| allowPicking              | Boolean | <code v-pre>true</code> | <code v-pre>optional</code> When true, each glTF mesh and primitive is pickable with Scene#pick. |
| incrementallyLoadTextures | Boolean | <code v-pre>true</code> | <code v-pre>optional</code> Determine if textures may continue to stream in after the model is loaded. |
| asynchronous              | Boolean | <code v-pre>true</code> | <code v-pre>optional</code> Determines if model WebGL resource creation will be spread out over several frames or block until completion once all glTF files are loaded. |
| clampAnimations | Boolean | <code v-pre>true</code> | <code v-pre>optional</code> Determines if the model's animations should hold a pose over frames where no keyframes are specified. |
| shadows | Number | <code v-pre>1</code> | <code v-pre>optional</code> Determines whether the model casts or receives shadows from each light source. <strong>DISABLED: 0, ENABLED: 1, CAST_ONLY: 2, RECEIVE_ONLY: 3, NUMBER_OF_SHADOW_MODES: 4, RECEIVE_ONLY: 3</strong> |
| debugShowBoundingVolume | Boolean | <code v-pre>false</code> | <code v-pre>optional</code> For debugging only. Draws the bounding sphere for each DrawCommand in the model. |
| debugWireframe | Boolean | <code v-pre>false</code> | <code v-pre>optional</code> For debugging only. Draws the model in wireframe. |
| heightReference | Number | <code v-pre>0</code> | <code v-pre>optional</code> Determines how the model is drawn relative to terrain. <strong>NONE: 0, CLAMP_TO_GROUND: 1, RELATIVE_TO_GROUND: 2</strong> |
| scene | Object | <code v-pre>false</code> | <code v-pre>optional</code> Must be passed in for models that use the height reference property. |
| distanceDisplayCondition | Object | | <code v-pre>optional</code> The condition specifying at what distance from the camera that this model will be displayed. <strong>Structure: { near: number, far: number }</strong> |
| color | Object|String|Array | <code v-pre>'WHITE'</code> | <code v-pre>optional</code> A color that blends with the model's rendered color. |
| colorBlendMode | Number | <code v-pre>0</code> | <code v-pre>optional</code> Defines how the color blends with the model. <strong>HIGHLIGHT: 0, REPLACE: 1, MIX: 2</strong> |
| colorBlendAmount | Number | <code v-pre>0.5</code> | <code v-pre>optional</code> Value used to determine the color strength when the colorBlendMode is MIX. A value of 0.0 results in the model's rendered color while a value of 1.0 results in a solid color, with any value in-between resulting in a mix of the two. |
| silhouetteColor | Object|String|Array | <code v-pre>'RED'</code> | <code v-pre>optional</code> The silhouette color. If more than 256 models have silhouettes enabled, there is a small chance that overlapping models will have minor artifacts. |
| silhouetteSize | Number | <code v-pre>0.0</code> | <code v-pre>optional</code> The size of the silhouette in pixels. |
| clippingPlanes | Object | | <code v-pre>optional</code> The ClippingPlaneCollection used to selectively disable rendering the model. |
| debugWireframe | Boolean | <code v-pre>true</code> | <code v-pre>optional</code> Determines if a Draco encoded model is dequantized on the GPU. This decreases total memory usage for encoded models. |
| credit | Object|String | | <code v-pre>optional</code> A credit for the model, which is displayed on the canvas. |</p>
<hr>
<ul>
<li>Refer to the official document: <strong><a href="https://cesium.com/docs/cesiumjs-ref-doc/Model.html">Model</a></strong></li>
</ul>
<h2>Events</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>name</th>
<th>parameter</th>
<th>description</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer, cesiumObject}</td>
<td>Triggers when the component is ready. It returns a core class of Cesium, a viewer instance, and the cesiumObject.</td>
</tr>
<tr>
<td>readyPromise</td>
<td>model</td>
<td>Triggers when the model is ready for use.</td>
</tr>
<tr>
<td>mousedown</td>
<td>{button,surfacePosition,target,type,windowPosition}</td>
<td>Triggered when the mouse is pressed on this primitive.</td>
</tr>
<tr>
<td>mouseup</td>
<td>{button,surfacePosition,target,type,windowPosition}</td>
<td>Triggered when the mouse bounces on the primitive.</td>
</tr>
<tr>
<td>click</td>
<td>{button,surfacePosition,target,type,windowPosition}</td>
<td>Triggered when the mouse clicks on the primitive.</td>
</tr>
<tr>
<td>dblclick</td>
<td>{button,surfacePosition,target,type,windowPosition}</td>
<td>Triggered when the left mouse button double-clicks the primitive.</td>
</tr>
<tr>
<td>mousemove</td>
<td>{button,surfacePosition,target,type,windowPosition}</td>
<td>Triggered when the mouse moves to this primitive.</td>
</tr>
</tbody>
</table>
<hr>
</section></template>

<script>
    export default {
      data() {
        return {
          url: './statics/SampleData/models/CesiumAir/Cesium_Air.gltf',
          modelMatrix: {}
        }
      },
      methods: {
        ready(cesiumInstance) {
          const { Cesium, viewer } = cesiumInstance
          this.viewer = viewer
          this.modelMatrix = Cesium.Transforms.eastNorthUpToFixedFrame(Cesium.Cartesian3.fromDegrees(105, 38, 10000))
        },
        readyPromise(model) {
          const boundingSphere = Cesium.BoundingSphere.transform(model.boundingSphere, model.modelMatrix)
          this.viewer.scene.camera.flyToBoundingSphere(boundingSphere)
        }
      }
    }
  </script>